<template>
    <div id="app">
        <Header :nickname="nickname" :openId="openId"  :isHistory="isHistory" @rightClick="handRightClick"></Header>
        <router-view :nickname="nickname" :openId="openId" :headimgurl="headimgurl" class="animated"/>
    </div>
</template>

<script>
import Header from './components/eleCom/header'
import axios from 'axios'
import common from './components/common/common.js'
import { MessageBox } from 'mint-ui';

export default {
    name: 'App',
     data(){
            return {
                code: '',
                nickname: '',
                openId: '',
                headimgurl: '',
                isHistory: false,
                show: false
            }
        },
        components: {
           Header,
           MessageBox
        },
        created(){

        },
        mounted(){
            this.code = this.$route.query.code
            axios.get(common.baseUrl+'/request', {
                params:{
                    code: this.code
                }
            }).then((res) => {
                if(res.data.errcode){
                    MessageBox.alert('页面授权已过期，请重新进入').then(action => {
                        window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxd55316e683616eda&redirect_uri=http://www.dadiandesign.com/&response_type=code&scope=snsapi_userinfo&state=state123#wechat_redirect'
                    });
                }
                //拿到用户的昵称和openId
                this.nickname = res.data.nickname
                this.openId = res.data.openid
                this.headimgurl = res.data.headimgurl
            }).catch((err) => {
                console.log(err)
            })

        },
        methods: {
            handRightClick(flag){
                this.isHistory = flag
            }
        }

}
</script>

<style>
html,body{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}

.mint-msgbox{
    font-size: 30px;
}
.mint-msgbox-header {
    height: 60px;
    line-height: 60px;
}
.mint-msgbox-title{
    font-size: 30px;
}
.mint-msgbox-content{
    height: 80px;
}
.mint-msgbox-message{
    line-height: 80px;
}
.mint-msgbox-btns{
    height: 80px;
    line-height: 80px;
}
.mint-msgbox-btn{
    font-size: 30px;
}

</style>





